$active-color: #53b8c6;

header {
  width: 100%;
  background-color: #41B3A3 !important;
  height: 50px;
  color: white;
  min-height: 80px;

  .email {
    font-size: 26px;
  }

  .left-header {
    height: 100%;
    display: inline-block;
  }

  .right-header {
    float: right;
    display: inline-block;

    .money-header {
      float: left;
      padding-right: 20px;
      padding-top: 29px;
      font-size: 18px;
    }

    .profile-header {
      padding-right: 20px;
      padding-top: 13px;
      float: left;
    }
  }

  .avatar-header {
    margin-bottom: 5px;
  }
}

.content {
  width: 100%;
}

.content-admin {
  .left-content {
    // padding-left: 20px;
  }

  .right-content {
    padding-left: 5px !important;

    .results {
      .result-item {
        border: 1px solid black;
      }
    }
  }

  .header-content {
    text-align: center;
  }

  .items {
    // padding-left: 20px;

    .grid-image-content {
      position: relative;

      .image-item {
        width: 100%;
      }

      .icon-image-item {
        position: absolute;
      }

      .name-item {
        text-align: center;
      }
    }
  }

  .results {
    .result-item {
      width: 95%;
      display: block;
      margin-left: auto;
      margin-right: auto;
      border-radius: 50%;
    }
  }
}

.group-button {
  text-align: center;
  margin-top: 10px;

  button {
    margin-top: 15px;
    margin-left: 5px;
  }
}

.message-player {
  height: 100px;
  text-align: center;

  td {
    font-weight: bold;
  }
}

.login-form {
  margin-top: 100px;

  .sign-in {
    padding-left: 150px;
  }
}

.shake {
  animation: shake 0.5s;
  animation-iteration-count: infinite;
}


.main-menu {
  display: inline-block;
  list-style-type: none;
  height: 100%;
  margin: 0;
  padding: 29px 0 0 20px;

  li {
    display: inline-block;
    height: 100%;

    a {
      display: block;
      color: white;
      text-align: center;
      padding: 0px 36px;
      text-decoration: none;
      font-weight: bold;
      height: 40%;
    }

    a.active {
      border-bottom: 5px solid #23085a;
    }
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }

  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }

  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }

  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }

  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }

  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }

  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }

  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

.hidden-mobile-menu {
  bottom: -60px !important;
}

.mobile-menu {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
  transition: bottom 0.6s;
  background: white;

  ul {
    width: 100%;
    display: inline-block;
    list-style-type: none;
    padding: 0;
    margin: 0;

    li {
      text-align: center;
      width: 50%;
      height: 100%;
      // display: inline-block;

      display: inline-flex;
      justify-content: center;
      align-items: center;
      border-left: 1px solid #c8c8c8;
      border-top: 1px solid #c8c8c8;
      border-bottom: 1px solid #c8c8c8;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      -ms-box-sizing: border-box;
      box-sizing: border-box;

      a {
        padding-top: 15px;
        height: 100%;
        width: 100%;
        font-size: 10px;
        text-decoration: none;

        .caption {
          color: gray;
          display: block;
        }
      }

      .icon {
        color: black;
      }

      .active {
        color: $active-color !important;

        .icon {
          color: $active-color !important;
        }

        span {
          color: $active-color !important;
        }
      }
    }
  }
}

